
.logo-nav {
  display: flex;
  align-items: center;
}
.logo-word {
  font-weight: 650;
  font-size: 25px;
}
.logo {
  text-align: center;
  transition: 0.5s;
  cursor: pointer;
}
.logo:hover {
  /* transform: translateY(10px); */
  transform: translateX(10px);
  transition: 0.5s ease-in;
  cursor: pointer;
}

.logo img {
  width: calc(100%-30%);
  object-fit: contain;
  z-index: 101;
  position: sticky;
  top: 0;
}

.menu a {
  font-weight: 600;
  font-size: 1rem;
  color: #212245;
  transition: 0.3s;
  text-decoration: none;
}
.user a {
  text-decoration: none;
}
.menu a:hover {
  color: #1673ff;
}
.active__menu {
  color: #1673ff !important;
}
.cart__icon i,
.user i,
.mobile__menu i {
  font-size: 1.3rem;
  color: #212245;
  cursor: pointer;
}

.cart__icon {
  position: relative;
}

.cart__badge {
  position: absolute; /* Dng  thit lp v tr ca mt phn t nhng n s lun nm trong mt phn t m ang l relative. */
  top: 18px;
  right: -10px;
  background: #df2020;
  color: #fff;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  font-size: 0.8rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mobile__menu {
  font-weight: 600;
  display: none;
}

.header__shrink {
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 5px 5px 15px -5px #fde4e4;
}

.documentation {
  font-weight: 600;
  color: black;
}

@media only screen and (max-width: 992px) {
  .logo img {
    width: calc(100% - 70%);
  }
  .logo h5 {
    font-size: 0.8rem;
    margin-top: -18px;
  }
  .navigation {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.425);
    z-index: 99;
    display: none;
  }

  .menu {
    position: absolute;
    top: 0px;
    right: 0;
    width: 250px;
    height: 100%;
    background: #fff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 100;
  }
  .menu a {
    font-size: 0.9 rem;
  }

  .mobile__menu {
    display: block;
  }
  .cart__icon i,
  .user i,
  .mobile__menu i {
    font-size: 1rem;
  }

  .show__menu {
    display: block;
  }
}

@media only screen and (max-width: 768px) {
  .header {
    width: 100%;
    height: 50px;
    line-height: 50px;
  }

  .logo h5 {
    margin-top: 0px;
  }
}
