.homePage__img {
  width: 100%;
  position: -webkit-sticky;
  z-index: -1;
  mask-image: linear-gradient(to bottom, rgb(0, 0, 0, 1), rgb(0, 0, 0, 0));
}

textarea:focus,
input:focus {
  outline: none;
}
.container__header {
  position: sticky;
  z-index: 100;
  top: 0;
}
.header {
  height: 70px;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  margin-bottom: 5px;
  z-index: 100;
  padding: 5px 30px 5px 30px;
  width: 100%;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.header__feature {
  height: 39px;
  display: flex;
  background-color: #21225936;

  width: 100%;
  justify-content: center;
}
.header__featureTitle {
  font-size: 15px;
  font-weight: 600;
  display: flex;
  position: relative;
  width: 20%;
  justify-content: flex-end;
  align-items: center;
  right: 30px;
}
.header__featureContainer {
  display: flex;
  justify-content: space-between;
  width: 60%;
}
.header__featureContainerOption {
  margin: 0px 10px 0px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.function {
  height: 39px;
  display: flex;
  position: sticky;
  background-color: #fa7268;
  width: 100%;
}

.header__nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: baseline;
  width: 100%;
}

.header__nav a {
  margin-right: 20px;
  text-decoration: none;
  color: #000000;
  font-size: 13px;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  height: 39px;
}
.header__optionInLine {
  font-size: 13px;
}
.header__createAccount {
  height: 30px;
  width: 150px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 600;
  line-height: 20px;
  font-size: 15px;
  color: #fff;
  text-align: center;
  text-shadow: #000000 0px 1px 0px;
  background-color: #fa7268;
  border-radius: 15px;
  padding: 5px 20px 0 20px;
}
.header__option:hover {
  cursor: pointer;
  transform: scale(1.02);
}

.header__featureContainerOption:hover {
  cursor: pointer;
  transform: scale(1.02);
}
