
.payment
{
    border:2px solid #f2f2f2;
    height:340px;
    border-radius:20px;
    background:#fff;
    margin-bottom: 70px;
}
.payment_header
{
   background:#00235b;
   padding:20px;
   border-radius:20px 20px 0px 0px;
   
}
.payment1_header
{
   background: #00235b;
   padding:20px;
   border-radius:20px 20px 0px 0px;
   
}

.check
{
   margin:0px auto;
   width:50px;
   height:50px;
   border-radius:100%;
   background:#fff;
   text-align:center;
}

.check i
{
   vertical-align:middle;
   line-height:50px;
   font-size:30px;
   color: #00235b;
}

.check1
{
   margin:0px auto;
   width:50px;
   height:50px;
   border-radius:100%;
   background:#fff;
   text-align:center;
}

.check1 i
{
   vertical-align:middle;
   line-height:50px;
   font-size:30px;
