}

.content1 
{
    text-align:center;
}

.content1  h1
{
    font-size:25px;
    padding-top:25px;
    padding-bottom: 15px;

}
.content1 p{
    padding-bottom: 15px;

}

.content1 span
{
    padding-top: 10px;
    width:200px;
    height:35px;
    color:#fff;
    border-radius:30px;
    padding:5px 10px;
    margin-right: 20;
    background: #00235b;
    transition:all ease-in-out 0.3s;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
}

.content1 span:hover
{
    text-decoration:none;
    background:#fff;
    border: 1px solid #00235b;
    color: #00235b;
}
