* {
  font-family: sans-serif;
}
.edgebutton {
  width: 20px;
  height: 20px;
  background: #eee;
  border: 1px solid #fff;
  cursor: pointer;
  border-radius: 50%;
  font-size: 12px;
  line-height: 1;
}

.edgebutton:hover {
  box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.08);
}

.edgebutton-foreignobject div {
  background: transparent;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
}

.fa-trash:hover {
  color: red;
}

.textIcon {
  display: inline;
  background-color: #1e3150;
  padding: 6px 7px;
  border-radius: 9px;
  margin-right: 7px;
}

.replyIcon {
  display: inline;
  background-color: #1e3150;
  padding: 4px 5px;
  border-radius: 9px;
  margin-right: 7px;
}
#message {
  border-radius: 12px;
  font-size: 17px;
  background: #f2f3f4;
  padding: 10px;
  border: 0.5px solid #ccedfc;
  max-height: 100px;
  overflow: hidden;
}
#message:hover {
  background: #ebf5fb;
  border: 0.5px solid #3498db;
  max-height: 100%;
}
#intent {
  border: 0px solid #ec7063;
}
#intent:hover {
  border: 1px solid #f5b041;
  background: #fffaf4 !important;
}

#deleteIcon {
  position: absolute;
  top: -2px;
  /* left: 303px; */
  right: -13%;
  width: 35px;
  border-radius: 10px;
  visibility: hidden;
}
#addCondition {
  display: none;
}
#node:hover > #deleteIcon {
  visibility: visible;
}
#node:hover {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
#node:hover #addCondition {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.hoverSession {
  width: 70px;
  height: 42px;
  position: absolute;
  top: 0px;
  right: -12%;
}
#deleteEdgeIcon {
  visibility: visible;
}
.react-flow__edge-path:hover #deleteEdgeIcon {
  visibility: visible;
}
#setting:hover {
  color: black;
}
#setting {
  color: #808b96;
  font-size: 22px;
  margin: 0px 6px;
}
.deleteCondition {
  display: none;
}
#intent:hover .deleteCondition {
  display: inline;
}
.inputKeyword:hover .deleteCondition {
  display: inline;
}

/* INTENT */
.actionTable {
  display: inline;
  background: red;
  margin: 0px 5px;
  padding: 4px;
  border-radius: 6px;
  width: 20px;
  font-size: 15px;
}
.tableData {
  margin: 15px 0px;
  font-size: 17px;
  font-family: sans-serif;
}
.tableData * {
  color: #1e3150;
}
.btn-table {
  margin-right: 5px;
  min-width: 113px;
}
#btn-import {
  border: none !important;
  background: #56cc6e !important;
  padding: 6px 12px !important;
  display: block !important;
  color: white !important;
}
.btn-import-excel {
  width: fit-content !important;
}
.btn-export-excel {
  width: 165px !important;
}
