.hero__content{
    padding-top: 70px;
}
.hero__title {
    font-size: 2.3rem;
    line-height: 55px;
}

.hero__title span{
    color: #df2020;
}

.hero__content p{
    font-size: 1rem;
    line-height: 30px;
    color: #777;
}
.hero__btns button{
    border: none;
    outline: none;
    padding: 7px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 600;
}
.order__btn{
    background: #212245;
    color: #fff;
    transition: 0.3s;
}
.order__btn:hover{
    background: #df2020;
}
.all__kits-btn {
    background: transparent;
    border: 1px solid #212245 !important;
}
.all__kits-btn a{
    color: #212245;
}
.all__kits-btn a:hover{
    color: #df2020;
}
.hero__service p{
    color: #212245;
    font-weight: 600;
    font-size: 0.9rem;
}
.shipping__icon i{
    padding: 5px;
    background: #df2020;
    color: #fff;
    border-radius: 50%;
    font-weight: 500 !important;
    font-size: 1rem;
}